import { useState, useEffect } from "react";
import BMeetupList from "../components/meetups/BMeetupList";
 
function Blogs() {
    const [isLoading, setIsLoading] = useState(true);
    const [loadedMeetups, setLoadedMeetups] = useState([]);
    useEffect(() => {
      setIsLoading(true);
      fetch("https://react-bts-default-rtdb.firebaseio.com/meetups.json"
      )
        .then((response) => {
          return response.json();
        })
        .then((data) => {
          const meetups = [];
  
          for (const key in data) {
            const meetup = {
              id: key,
              ...data[key]
            };
            meetups.push(meetup);
          }
          setIsLoading(false);
          setLoadedMeetups(meetups);
        });
    }, []);
  
    if (isLoading) {
      return (
        <section>
          <p>Loading...</p>
        </section>
      );
    }
    return (
      <section>
        <h1>Blogs</h1>
        <BMeetupList meetups={loadedMeetups} />
      </section>
    );
  }
  
 
export default Blogs;